@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&subset=cyrillic-ext);
@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed&subset=cyrillic-ext);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  font-family: "Roboto", sans-serif;
  min-height: 100%;
}
* {
  box-sizing: border-box;
}

a {
  color: #727e86;
}

a,
a:hover,
a:visited,
a:active {
  text-decoration: none;
}
button {
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
}
a:hover {
  cursor: pointer;
}

section {
  margin-top: 2em;
  margin-bottom: 2em;
}

/* Bootstrap */
.nav-link.active {
  color: #ff0000;
}

h2,
.h2 {
  margin-bottom: 1.5em;
  margin-top: 2em;
}

.btn-outline-primary {
  color: #727e86;
  border-color: #727e86;
}

.btn-outline-primary:hover {
  background-color: #727e86;
  border-color: #727e86;
}
.App {
  height: 100%;
}

.ItemUser{max-width:420px;width:100%;height:73px;background:#f3f3f3;border-radius:8px;padding:10px;position:relative;box-sizing:border-box}.ItemUser__element{display:flex;margin-bottom:7px}.ItemUser__element:last-child{margin-bottom:0}.ItemUser__element-grei{font-family:Roboto;font-style:normal;font-weight:normal;font-size:11px;line-height:13px;color:#a3a3a3;margin-right:7px}.ItemUser__element-black{font-family:Roboto;font-style:normal;font-weight:normal;font-size:11px;line-height:13px;color:#000000}.ItemUser__link{position:absolute;bottom:10px;right:10px;font-family:Roboto;font-style:normal;font-weight:normal;font-size:12px;line-height:14px;color:#4b51ef}

.UserList{padding-top:19px;padding-left:34px;padding-right:34px}.UserList__title{font-family:Roboto;font-style:normal;font-weight:bold;font-size:14px;line-height:16px;color:#000000;margin-bottom:15px}.UserList__row{width:100%;margin-bottom:10px}.UserList__item{margin-bottom:15px}.UserList__item:last-child{margin-bottom:0px}.UserList__pagination{font-family:Roboto;font-style:normal;font-weight:normal;font-size:12px;line-height:14px;text-align:right;color:#000000;margin-right:30px}

.btn{padding-left:15px;padding-right:15px;padding-top:5px;padding-bottom:5px;font-family:Roboto;font-style:normal;font-weight:normal;font-size:12px;line-height:14px;border-radius:5px;display:inline-block;color:#ffffff}.btn.blue{background:#4b51ef}.btn.green{background:#52cf4f}.btn.disabled{background-color:#afafaf}

.SortList{max-width:152px;width:100%;height:100%;background:#bdbdbd;padding:20px;padding-top:35px;display:flex;flex-direction:column}.SortList_title{margin-bottom:10px}.title{font-family:Roboto;font-style:normal;font-weight:normal;font-size:12px;line-height:14px;color:#000000}.SortList_row{flex:1 1 auto}.SortList_item{margin-bottom:10px}.SortList_item:last-child{margin-bottom:0}

.container{max-width:665px;width:100%;margin:0 auto}.main{min-height:100%}.main .row{display:flex;width:100%;min-height:100%}.main .col{width:77.14%}.main .col.sort{width:22.86%;min-height:100%}


.ProfileUser{padding-top:13px;padding-left:36px;padding-right:41px;height:100vh}.ProfileUser__top{display:flex;justify-content:space-between;align-items:center;margin-bottom:16px}.ProfileUser__title{font-family:Roboto;font-style:normal;font-weight:bold;font-size:14px;line-height:16px;color:#000000}.ProfileUser__edit{margin-right:5px}.form .form__row{background:#ffffff;border:1px solid #f3f3f3;border-radius:8px;padding-top:20px;padding-left:12px;padding-right:6px;padding-bottom:19px;margin-bottom:10px}

.form__item{margin-bottom:10px}.form__item:last-child{margin-bottom:0}.label{font-family:Roboto;font-style:normal;font-weight:normal;font-size:8px;line-height:9px;color:#000000;display:flex;flex-direction:column}.form__element{width:211px;background:#ffffff;border:1px solid #d8d8d8;box-sizing:border-box;border-radius:5px;padding-left:10px;padding-top:5px;padding-bottom:5px;font-family:Roboto;font-style:normal;font-weight:normal;font-size:10px;line-height:12px;color:#000000;margin-top:3px}.form__element.textarea{width:100%;height:55px}.form__element.disabled{color:rgba(0,0,0,0.3)}.form__bottom{display:flex;justify-content:flex-end}.form__submit{margin-left:auto;margin-right:0px}

